<template>
    <a-layout id="layout">
        <a-layout-sider id="sider" width="300px" theme="light">
            <a-card id="card" title="">
                <p>{{this.nickname}}</p>
                <p>{{this.gender}}</p>
                <p>{{this.old}}</p>
            </a-card>
            <a-menu theme="light"
                    mode="inline"
                    @click="onMenuClick"
                    :selectedKeys="[this.$store.state.userfunction]">
                <a-menu-item key="1"><router-link :to="'/users/'+this.$store.state.userid+'/shelf'"></router-link></a-menu-item>
                <a-menu-item key="2"><router-link :to="'/users/'+this.$store.state.userid+'/comments'"></router-link></a-menu-item>
                <a-menu-item key="3"><router-link :to="'/users/'+this.$store.state.userid+'/update'"></router-link></a-menu-item>
            </a-menu>
        </a-layout-sider>
        <div id="window">
            <router-view/>
        </div>
    </a-layout> 
</template>

<script>
    export default {
        created() {
            this.$store.commit('select', '1');
            this.getuserinfo();
        },
        data() {
            return {
                nickname: "",
                gender: "",
                old: 0
            }
        },
        methods: {
            onMenuClick(e) {
                this.$store.commit('selectfunc', e.key);
            },
            getuserinfo() {
                let _this = this;
                this.axios({
                    method: 'GET',
                    url: this.$store.state.host + '/users/' + this.$store.state.userid,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
                        'Authorization': window.localStorage.getItem('token')
                    }
                }).then(function (res) {
                    let status = res.data.status;
                    if (status == 200) {
                        _this.nickname = res.data.data[0].nickname;
                        _this.old = res.data.data[0].old;
                        _this.gender = "";
                        if (res.data.data[0].gender == 1) {
                            _this.gender = "";
                        }
                        return;
                    }
                    switch (status) {
                        case 400:
                            alert("");
                            break;
                        case 401:
                            alert("");
                            window.localStorage.setItem('token', '');
                            _this.$store.commit('deletelogin');
                            window.location.href = "/login";
                            break;
                        case 403:
                            alert("");
                            break;
                        case 404:
                            window.localStorage.setItem('token', '');
                            _this.$store.commit('deletelogin');
                            window.location.href = "/login";
                            alert("");
                            break;
                        case 500:
                            alert("");
                            break;
                        default:
                            alert("");
                    }
                }).catch(function (err) {
                    alert(err);
                });
            }
        }
    }
</script>

<style scoped>
    #layout {
        width: 100%;
        z-index: -1;
        background-color: rgba(255, 255, 255, 1);
    }
    #sider {
        padding: 30px 0;
        height: 100%;
        position: fixed;
    }
    #card {
        text-align: left;
        border-radius: 20px;
        background: rgba(107, 171, 225, .75);
        height: 200px;
        width: 250px;
        margin: 0 auto 40px;
    }
    #window{
        width: 100%;
        height: 100%;
    }
</style>